<template>
    <!-- 助航结束 -->
    <div class="sail-over paper">
        <div class="scroll-wrapper">
            <div class="location-info">
                <div class="sn">{{info.sn_name}}</div>
                <div class="en">{{info.en_name}}</div>
            </div>
            <div class="sail-info theme-background-default">
                <div class="sail-info-item">
                    <div>
                        <div class="text">开始时间</div>
                        <div class="value theme-text-secondary">{{info.sn_time | formatDate('MM-DD HH:mm')}}</div>
                    </div>
                    <div>
                        <div class="text">结束时间</div>
                        <div class="value theme-text-secondary">{{info.en_time | formatDate('MM-DD HH:mm')}}</div>
                    </div>
                </div>
                <i class="line"></i>
                <div class="sail-info-item">
                    <div>
                        <div class="text">实际航行里程</div>
                        <div class="value theme-text-secondary">
                            {{info.actual_distance | formatDistance}}
                            {{info.actual_distance > 1000 ? 'km' : 'm'}}
                        </div>
                    </div>
                    <div>
                        <div class="text">规划航行里程</div>
                        <div class="value theme-text-secondary">
                            {{info.plan_distance | formatDistance}}
                            {{info.plan_distance > 1000 ? 'km' : 'm'}}
                        </div>
                    </div>
                </div>
                <i class="line"></i>
                <div class="sail-info-item">
                    <div>
                        <div class="text">实际航行时间</div>
                        <div class="value theme-text-secondary">{{info.actual_time | secondToDate('', true)}}</div>
                    </div>
                    <div>
                        <div class="text">规划航行时间</div>
                        <div class="value theme-text-secondary">{{info.plan_time | secondToDate('', true)}}</div>
                    </div>
                </div>
            </div>
            <div class="other-info border-1px-b">
                <img :src="require('../../../static/icon/other_bridge.png')">
                <p>
                    经过了
                    <span class="theme-text-color">{{info.passed_bridge}}</span>
                    座桥梁，
                    <span class="theme-text-color">{{info.passed_lock}}</span>
                    个船闸
                </p>
            </div>
            <div class="button-wrapper">
                <mu-button class="button" @click="close" flat>返回首页</mu-button>
            </div>
        </div>
    </div>
    <!-- 助航结束 -->
</template>

<script>
    import {routeMixins} from "../../common/mixins"
    import {formatDate, secondToDate} from "../../common/utils/time"
    import {submitLogger} from "../../api/sys"

    export default {
        name: "sail-over",
        mixins: [routeMixins],
        // data() {
        //     return {
        //         info: {
        //             sn_name: '',
        //             en_name: '',
        //             sn_time: '',
        //             en_time: '',
        //             actual_distance: '',
        //             plan_distance: '',
        //             actual_time: '',
        //             plan_time: '',
        //             passed_bridge: '',
        //             passed_lock: ''
        //         }
        //     }
        // },
        computed: {
            info() {
                return this.$route.query.data || {}
            }
        },
        activated() {
            const ship = this.$route.query.ship || {}

            window.Arcgis && window.Arcgis.setFinishMask("1", function (e) {
                console.log(e)
            })

            submitLogger('route', {
                shipId: ship.id,
                actualDrivingDistance: this.info.actual_distance,
                actualDrivingTime: Number.parseInt(this.info.actual_time / 60),
                planDistanceTime: Number.parseInt(this.info.plan_time / 60),
                clearanceHeight: ship.pureHeight,
                draught: ship.draft,
                startTime: formatDate(this.info.sn_time, 'YYYY-MM-DD HH:mm:ss'),
                endTime: formatDate(this.info.en_time, 'YYYY-MM-DD HH:mm:ss'),
                goalName: this.info.en_name,
                startName: this.info.sn_name,
                planDistance: this.info.plan_distance,
                startLatitude: this.info.startLatitude,
                startLongitude: this.info.startLongitude,
                actualLatitude: this.info.actualLatitude,
                actualLongitude: this.info.actualLongitude,
                goalLatitude: this.info.goalLatitude,
                goalLongitude: this.info.goalLongitude,
            })
        },
        methods: {
            close() {
                this.resetRoutes()

                window.Arcgis && window.Arcgis.clearMask()

                this.$router.replace('/')
            }
        },
        filters: {
            formatDate: formatDate,
            secondToDate: secondToDate
        }
    }
</script>

<style lang="stylus">
    @import "../../common/stylus/variable.styl"
    @import "../../common/stylus/mixins.styl"

    .sail-over
        position absolute
        bottom 0
        left 0
        right 0
        width 100%
        height 300px
        overflow hidden

        .scroll-wrapper
            width 100%
            height 100%

            .location-info
                display flex
                flex-direction column
                justify-content space-around
                padding 10px
                height 65px
                background-color #7F8F9E

                .sn, .en
                    display flex
                    align-items center
                    height 24px
                    color #fff
                    font-size 16px
                    no-wrap()

                    &:before
                        content ''
                        display inline-block
                        margin 0 10px 5px
                        height 10px
                        width 10px
                        border 1px solid #ccc
                        border-radius 50%

                .sn:before
                    border-color #6CDA61

                .en:before
                    border-color #FB94A3

            .sail-info
                display flex
                align-items center
                padding 16.25px 15px
                height 120px

                .line:before
                    left-line(#E3E3E3)

                .sail-info-item
                    flex 1
                    display flex
                    flex-direction column
                    justify-content space-between
                    height 100%

                    .text
                        display block
                        line-height 1.74625
                        text-align center
                        font-size 14px

                    .value
                        display block
                        text-align center
                        font-size 16px

            .other-info
                display flex
                align-items center
                padding 0 20px
                height 50px
                vertical-align bottom

                img
                    display block
                    width 25px
                    height 25px

                p
                    margin-left .5em
                    font-size 16px
                    vertical-align bottom

                    span
                        font-size 1.3em

            .button-wrapper
                display flex
                align-items center
                flex-direction row-reverse
                padding 0 15px
                height 65px

                .button
                    border 1px solid #ccc
                    border-radius 100px
</style>



